import './App.css';
import React, { useState, useEffect,useContext } from 'react';
import DeleteIcon from '@mui/icons-material/Delete';
import { DispatchContext } from './TenttiSovellus'

//let arr1 = [{vastaus: "ei", booleanArvoVastaukselle: false, komponentinId: 1},{vastaus: "kyllä", booleanArvoVastaukselle: false, komponentinId: 2},{vastaus: "ehkä", booleanArvoVastaukselle: false, komponentinId: 3}]
//let arr2 = [{vastaus: "ei", booleanArvoVastaukselle: false, komponentinId: 4},{vastaus: "kyllä", booleanArvoVastaukselle: false, komponentinId: 5},{vastaus: "ehkä", booleanArvoVastaukselle: false, komponentinId: 6}]
//let arr3 = [{vastaus: "ei", booleanArvoVastaukselle: false, komponentinId: 7},{vastaus: "kyllä", booleanArvoVastaukselle: false, komponentinId: 8},{vastaus: "ehkä", booleanArvoVastaukselle: false, komponentinId: 9}]
//let arr4 = [{vastaus: "ei", booleanArvoVastaukselle: false, komponentinId: 10},{vastaus: "kyllä", booleanArvoVastaukselle: false, komponentinId: 11},{vastaus: "ehkä", booleanArvoVastaukselle: false, komponentinId: 12}]



const Vastaukset = ({ vastausVaihtoehdot, kysymys, kysymysIndex  })  => {
  const {dispatch} = useContext(DispatchContext)

    return (
      <>
        {vastausVaihtoehdot.map((vaihtoehto, index) => {
          return vaihtoehto.booleanArvoVastaukselle === true ? (
          <> 
            <div className="answerlines">
              <div className="answerline">
                <input type="checkbox" id={index} checked onClick={() => dispatch({type:"CHECK_VASTAUS",data: {pääkysymysIndex: kysymysIndex,vastausIndex: index}})}/> 
                <input id={`vastausInput${index}`} value={vaihtoehto.vastaus} type="text" onChange={(event) => dispatch({type:"EDIT_VASTAUS", data: {pääkysymysIndex: kysymysIndex,vastausIndex: index, vastaus:event.target.value}})}></input>
                <DeleteIcon onClick={() => dispatch({type:"DEL_VASTAUS",data: {pääkysymysIndex: kysymysIndex, vastausIndex: index}})}/>
              </div>
            </div> 
        </>) 
        :
        <> 
        <div className="answerlines">
          <div className="answerline">
            <input type="checkbox" id={index} onClick={() => dispatch({type:"CHECK_VASTAUS",data: {pääkysymysIndex: kysymysIndex,vastausIndex: index}})}/>
            <input id={`vastausInput${index}`} value={vaihtoehto.vastaus} type="text" onChange={(event) => dispatch({type:"EDIT_VASTAUS", data: {pääkysymysIndex: kysymysIndex,vastausIndex: index, vastaus:event.target.value}})}></input>
            <DeleteIcon onClick={() => dispatch({type:"DEL_VASTAUS",data: {pääkysymysIndex: kysymysIndex, vastausIndex: index}})}/>
          </div>
        </div> 
      </>
        })}
  </>
  );
}

export default Vastaukset;