import './App.css';
import React, { useState, useEffect, useReducer, createContext } from 'react';
import Tentti2 from './Tentti2'
import tenttiServices from './Services/tentit'
import Paper from '@mui/material/Paper';
import Login from './Login'
export const DispatchContext = createContext();


/**
 * 
 * /**React tehtävä:
 * 1) login ui -> login pyyntö serverille
 * 2) kun pyyntö palaa: token tallennus - localstorage
 * -> miten token heitetään localstorageen - google
 * 3) tuleviin post, get pyyntöihin - headers: auth: token
 */

function TenttiSovellus() {
  //onko kirjauduttu sisään - true, false -> laukaisee useEffectin?
  const [loggedIn, setLoggedIn] = useState(false)

  const [dataNoudettu, setDataNoudettu] = useState(true);
  //Tentit tila mapataan ja sieltä valitaan indeksin avulla näytettävä tentti tentit[valittuTentti]
  const [tentit, setTentit] = useState([]);
  //valittu tentti on indeksi renderöitävälle tentille tentit tilalistassa
  const [valittuTentti, setValittuTentti] = useState();
  
  useEffect(() => {

    if (dataNoudettu === false) {
    
       function fetchData() {
        console.log("use effect")
        let token = JSON.parse(localStorage.getItem("token"))
        let result = tenttiServices.getAll(token)
        // handle success
        result.then(result => {
          console.log("result data",result)
          setTentit(result)
          setDataNoudettu(true)
        })
        
          
      }
      fetchData()

    }
  }, [dataNoudettu])

  const dispatch = (o) => {
    let token = JSON.parse(localStorage.getItem('token'))
    //o = {type:"DEL_VASTAUS",data: {pääkysymysIndex: kysymysIndex,vastausIndex: index}
    let tentitKopio = JSON.parse(JSON.stringify(tentit))
    switch(o.type) {
      case "DEL_EXAM":
        console.log("Del exam o",o)
        tenttiServices.del(token,o.data.id)
        .then(function (response){
          console.log("delete response",response)
        })
        setValittuTentti()
        tentitKopio.forEach((element,index) => {
          if(element.id === o.data.id) {
            console.log("removed exam:",tentitKopio.splice(index,1))
          }
        });
        break;
      case "CREATE_KYSYMYS":
        //tarvitsee idn -> se id johon kohdistetaan put - kysymyksen id
        //tarvitsee kokonaisen muokatun objektin
        tentitKopio[valittuTentti].sisältö.push([{pääkysymys:o.data.kysymys, oikeaVastaus: 1, id: tentitKopio.length, vastausVaihtoehdot:[]}])
        tenttiServices.edit(token,tentitKopio[valittuTentti].id,tentitKopio[valittuTentti])
        .then(function (response){
          console.log("add question response",response)
        })
        break;
      case "DEL_KYSYMYS":
          tentitKopio[valittuTentti].sisältö.splice(o.data.pääkysymyksetIndex,1)
          tenttiServices.edit(token,tentitKopio[valittuTentti].id,tentitKopio[valittuTentti][0])
          .then(function (response){
            console.log("del question response",response)
          })
          break;
      case "EDIT_KYSYMYS":
        tentitKopio[valittuTentti].sisältö[o.data.pääkysymysIndex][0].pääkysymys = o.data.kysymys
        tenttiServices.edit(token,tentitKopio[valittuTentti].id,tentitKopio[valittuTentti])
        .then(function (response){
          console.log("edit question text",response)
        })
        break;
      case "CHECK_VASTAUS":
        let array = tentitKopio[valittuTentti].sisältö[o.data.pääkysymysIndex][0].vastausVaihtoehdot
        array[o.data.vastausIndex].booleanArvoVastaukselle = !array[o.data.vastausIndex].booleanArvoVastaukselle
        tentitKopio[valittuTentti].sisältö[o.data.pääkysymysIndex][0].vastausVaihtoehdot = array
        tenttiServices.edit(token,tentitKopio[valittuTentti].id,tentitKopio[valittuTentti])
        .then(function (response){
          console.log("Check answer response",response)
        })
        break;
      case "CREATE_VASTAUS":
        let arr = tentitKopio[valittuTentti].sisältö[o.data.pääkysymysIndex][0].vastausVaihtoehdot
        //huomioi komponentinid tässä, pitää tehdä funktio, joka tekee paremman id arvon
        arr.push({vastaus: o.data.vastausVaihtoehto, booleanArvoVastaukselle: false, komponentinId: arr.length})
        tentitKopio[valittuTentti].sisältö[o.data.pääkysymysIndex][0].vastausVaihtoehdot = arr
        tenttiServices.edit(token,tentitKopio[valittuTentti].id,tentitKopio[valittuTentti])
        .then(function (response){
          console.log("create answer response",response)
        })
        break;
      case "DEL_VASTAUS":
        tentitKopio[valittuTentti].sisältö[o.data.pääkysymysIndex][0].vastausVaihtoehdot.splice(o.data.vastausIndex,1)
        tenttiServices.edit(token,tentitKopio[valittuTentti].id,tentitKopio[valittuTentti])
        .then(function (response){
          console.log("del answer response",response)
        })
        break;
      case "EDIT_VASTAUS":
        tentitKopio[valittuTentti].sisältö[o.data.pääkysymysIndex][0].vastausVaihtoehdot[o.data.vastausIndex].vastaus = o.data.vastaus
        tenttiServices.edit(token,tentitKopio[valittuTentti].id,tentitKopio[valittuTentti])
        .then(function (response){
          console.log("edit answer response",response)
        })
        break;
      default: throw "Jokin vika"
      }
      setTentit(tentitKopio)
  }
  
   function lisääTentti (tentit) {
    let tenttiObject = {tenttiNimi:"testiTentti", sisältö:[[{pääkysymys: "testiTentti", oikeaVastaus: 1, id: 0, vastausVaihtoehdot:[{vastaus: "ei", booleanArvoVastaukselle: false, komponentinId: 0},{vastaus: "kyllä", booleanArvoVastaukselle: false, komponentinId: 1},{vastaus: "ehkä", booleanArvoVastaukselle: false, komponentinId: 2}]}],[{pääkysymys: "Onko JavaScript kivaa?", oikeaVastaus: 1, id: 0, vastausVaihtoehdot:[{vastaus: "ei", booleanArvoVastaukselle: false, komponentinId: 0},{vastaus: "kyllä", booleanArvoVastaukselle: false, komponentinId: 1},{vastaus: "ehkä", booleanArvoVastaukselle: false, komponentinId: 2}]}],[{pääkysymys: "Onko JavaScript kivaa?", oikeaVastaus: 1, id: 0, vastausVaihtoehdot:[{vastaus: "ei", booleanArvoVastaukselle: false, komponentinId: 0},{vastaus: "kyllä", booleanArvoVastaukselle: false, komponentinId: 1},{vastaus: "ehkä", booleanArvoVastaukselle: false, komponentinId: 2}]}]] }
    
    let token = JSON.parse(localStorage.getItem('token'))
    tenttiServices      
      .create(token,tenttiObject)      
      .then(returnedTentti => {   
        console.log("tee tentti jonka id on: ",returnedTentti.id )
             console.log("lisätty tentti:",returnedTentti)
             let tentitKopio = JSON.parse(JSON.stringify(tentit))
             tentitKopio.push(returnedTentti)
             setTentit(tentitKopio)
      })
      .catch(error => {     
    })
  }

  async function checkDB() {
    let result = await tenttiServices.getAll()
    console.log("checkdb",result.data)
    console.log("check valittu tentti listassa",valittuTentti)
    console.log("check tentit",tentit)
    console.log("check id",tentit[valittuTentti].id)
  }


  return (    
  <div>
    {(loggedIn === true) 
    ? 
    <div>
        {(dataNoudettu === true) && <div> {tentit.map((item,index) => <button onClick={() => {setValittuTentti(index)}}>Näytä tentti {index}</button> )}</div>}
        <button onClick={() =>lisääTentti(tentit)}>lisää tentti</button>
          <DispatchContext.Provider value={{dispatch}}>
            {(valittuTentti!==undefined) && <Tentti2 tentti={tentit[valittuTentti]} dispatch={dispatch} setTentit={setTentit} tenttiIndex={valittuTentti} tentit={tentit} tenttiID={tentit[valittuTentti].id}/>}
          </DispatchContext.Provider>
      <button onClick={() => checkDB()}>Tarkista tilanne</button>
    </div>
  :
  <Login loggedIn={loggedIn} setLoggedIn={setLoggedIn} setDataNoudettu={setDataNoudettu} />
  }
  </div>
  );
}


export default TenttiSovellus;