import './App.css';
import React, { useState, useEffect, useReducer } from 'react';
import { styled } from '@mui/material/styles';
import Container from '@mui/material/Container';
import Kysymys from './Kysymys'
import Grid from '@mui/material/Grid';
import Box from '@mui/material/Box';
import CreateQuestion from './CreateQuestion'
import DeleteExam from './DeleteExam';
import Paper from '@mui/material/Paper';


function Tentti2({ tentti, dispatch, setTentit, tenttiIndex, tentit, tenttiID  }) {
  
  useEffect(() => {
    console.log(tentit)
    let tentitKopio = JSON.parse(JSON.stringify(tentit))
    tentitKopio[tenttiIndex] = tentti
    console.log(tentitKopio)
    setTentit(tentitKopio)

 }, [tentti])


  return (    
    <Paper sx={{display:"flex", margin: "2%", width:"91.7%", justifyContent:"center", verticalAlign: "middle", alignItems: "center"}} elevation="3" > 
      <Container className="container"> 
        <p>tentin id: {tentti.id}</p>
        <Box sx={{width:"90%"}}> 
          {tentti.sisältö.map((setti,index) => (
            <div id={index} key={index}> 
              <Kysymys kysymysProp={setti} tentti={tentti} kysymykset={tentti.sisältö} pääkysymyksetIndex={index} dispatch={dispatch}/>
            </div>
          ))}
            <Grid>
              <CreateQuestion dispatch={dispatch}/>
            </Grid> 
            <Grid>
              <DeleteExam dispatch={dispatch} tenttiId={tenttiID} tenttiIndex={tenttiIndex}/>
            </Grid> 
          </Box>
      </Container>
    </Paper>
  );
}



export default Tentti2;