
import React, { useState } from 'react';
import Paper from '@mui/material/Paper';

const CreateQuestion = ({ dispatch, kysymysIndex })  => {
//[{vastaus: "ei", booleanArvoVastaukselle: false, komponentinId: 1}]
    const [kysymys, setKysymys] = useState(" ")

    const kysymysInputOnChange = (event) => {    
      setKysymys(event.target.value);
    }

  return (
    < >
      <Paper elevation="5"  sx={{ width:"100%",margin:"2%", padding: "1%"}}>
            <div className="answerlines">
              <div className="answerline">
                <input value={kysymys} type="text" id="inputLisääKysymys" onChange={kysymysInputOnChange}></input>
                <button  onClick={() => dispatch({type:"CREATE_KYSYMYS",data: {kysymys: kysymys}})}>Lisää kysymys</button>
              </div>
            </div>
      </Paper>
    </>
  );
}

export default CreateQuestion;