
import React, { useState } from 'react';
import Paper from '@mui/material/Paper';

const DeleteExam = ({ dispatch, tenttiId,tenttiIndex })  => {
console.log("delexam",tenttiId)
  return (
    < >
        <Paper elevation="5"  sx={{ width:"100%",margin:"2%", padding: "1%"}}>
          <div className="answerlines">
            <div className="answerline">
              <button  onClick={() => dispatch({type:"DEL_EXAM",data: {id: tenttiId, tenttiIndex: tenttiIndex}})}>Poista tentti</button>
            </div>
          </div>
      </Paper>
    </>
  );
}

export default DeleteExam;