
import React, { useState,useContext } from 'react';
import { DispatchContext } from './TenttiSovellus'

const AddAnswers = ({ kysymysIndex })  => {

  const {dispatch} = useContext(DispatchContext)

    const [vastaus, setVastaus] = useState(" ")

    const vastausInputOnChange = (event) => {    
      setVastaus(event.target.value);
    }

  return (
    < >
        <input placeholder="lisää vastaus" id="Lisää vastaus" onChange={vastausInputOnChange}></input>
        <button  onClick={() => dispatch({type:"CREATE_VASTAUS",data: {pääkysymysIndex: kysymysIndex,vastausVaihtoehto: vastaus}})}>Lisää vastaus</button>
    </>
  );
}

export default AddAnswers;