import './App.css';
import React, { useState, useEffect, useReducer } from 'react';
import { styled } from '@mui/material/styles';
import Container from '@mui/material/Container';
import Grid from '@mui/material/Grid';
import Box from '@mui/material/Box';
import Paper from '@mui/material/Paper';
import tenttiServices from './Services/tentit'
import Register from './Register';
import Button from '@mui/material/Button';

function Login({ loggedIn, setLoggedIn, setDataNoudettu }) {
    const [username, setUsername] = useState("")
    const [password, setPassword] = useState("")
    const [showRegisterComponent, setShowRegisterComponent] = useState(false)
    
    
  const handleUsernameChange = (event) => {    
    //console.log(event.target.value)    
    setUsername(event.target.value);  
  }
  const handlePasswordChange = (event) => {    
    //console.log(event.target.value)    
    setPassword(event.target.value);  
  }

  const loginFunction = (e) => {
    e.preventDefault()
    console.log("loginfunction",username, password)

    tenttiServices.login(username, password)
    .then(function (response) {
        // handle success - localstorageen tallennus
        console.log("token:",response.data);
        //tähän local storage
        let avain = "token"
        localStorage.setItem(avain, JSON.stringify(response.data))
        setLoggedIn(true)
        setDataNoudettu(false)
      })
      .catch(function (error) {
        // handle error
        console.log(error);
      })
  }

  const register = (e) => {
    e.preventDefault()
    console.log("register")
    setShowRegisterComponent(true)
  }


  return (
    <div>
    {(showRegisterComponent === true) 
      ?    
  
    <Register setShowRegisterComponent={setShowRegisterComponent}/>

    :
    <Paper sx={{display:"flex", margin: "2%", padding:"2%", width:"400px", height: "100px", justifyContent:"center", verticalAlign: "middle", alignItems: "center"}} elevation="3" > 
      <Container className="container"> 
        <Box sx={{width:"90%"}}> 
          <Grid>
              <form onSubmit={loginFunction}>
                <div sx={{justifyContent:"center", verticalAlign: "middle", alignItems: "center"}}>
                  <div sx={{margin: "2%"}}>
                      username: <input value={username} onChange={handleUsernameChange}/>
                  </div>
                  <div  sx={{margin: "2%"}}>
                      password: <input type="password" value={password} onChange={handlePasswordChange}/></div>
                  <div>
                      <Button type="submit" variant="contained" size="medium"  sx={{margin: "2%"}}>Login</Button>
                      <Button onClick={register} variant="contained" size="medium"  sx={{margin: "2%"}}>Register</Button>
                  </div>
                </div>
              </form>
          </Grid> 
        </Box>
    </Container>
  </Paper>
    }
  </div>
  );
}


export default Login;