import './App.css';
import React, { useState, useEffect, useContext } from 'react';
import Box from '@mui/material/Box';
import Grid from '@mui/material/Grid';
import Vastaukset from './Vastaukset'
import AddAnswers from './AddAnswers'
import DeleteIcon from '@mui/icons-material/Delete';
import Paper from '@mui/material/Paper';
import { DispatchContext } from './TenttiSovellus'

//kysymys rakenne:
//let listaPääkysymyksiä1 = [{pääkysymys: "Onko JavaScript kivaa?", id: 0, vastausVaihtoehdot:arr1}]

const Kysymys = ({ kysymysProp, pääkysymyksetIndex})  => {
  const {dispatch} = useContext(DispatchContext)

  return (
    <Box>
      <Paper elevation="14" sx={{ width:"100%",margin:"2%", padding: "1%"}}>
        {kysymysProp.map((kysymysElementti, index) => (
            <Grid className="textbox"> 
               <> <input value={kysymysElementti.pääkysymys} type="text" onChange={(event) => dispatch({type:"EDIT_KYSYMYS", data: {pääkysymysIndex: pääkysymyksetIndex, kysymys:event.target.value}})}></input>  <DeleteIcon onClick={() => dispatch({type:"DEL_KYSYMYS",data: {pääkysymyksetIndex: pääkysymyksetIndex}})}/> </>
              <Vastaukset vastausVaihtoehdot={kysymysElementti.vastausVaihtoehdot} kysymysIndex={pääkysymyksetIndex}/> 
              <AddAnswers kysymysIndex={pääkysymyksetIndex}/> 
            </Grid>
            ))}
       </Paper>    
    </Box>
  );
}

export default Kysymys;