import './App.css';
import React, { useState, useEffect, useReducer } from 'react';
import { styled } from '@mui/material/styles';
import Container from '@mui/material/Container';
import Grid from '@mui/material/Grid';
import Box from '@mui/material/Box';
import Paper from '@mui/material/Paper';
import tenttiServices from './Services/tentit'
import Button from '@mui/material/Button';

function Register({ setShowRegisterComponent }) {
    const [username, setUsername] = useState("")
    const [password, setPassword] = useState("")
    
  const handleUsernameChange = (event) => {    
    //console.log(event.target.value)    
    setUsername(event.target.value);  
  }
  const handlePasswordChange = (event) => {    
    //console.log(event.target.value)    
    setPassword(event.target.value);  
  }

  const registerFunction = (e) => {
    e.preventDefault()
    console.log("registerFunction: ",username, password)

    tenttiServices.register(username, password)
    .then(function (response) {
        console.log(response)
     
      })
      .catch(function (error) {
        // handle error
        console.log(error);
      })
      setShowRegisterComponent(false)
  }

 

  return (    
    <Paper sx={{display:"flex", margin: "2%", width:"50%", justifyContent:"center", verticalAlign: "middle", alignItems: "center"}} elevation="3" > 
      <Container className="container"> 
        <Box sx={{width:"90%"}}> 
            <Grid sx={{margin: "2%"}}>
                <form onSubmit={registerFunction} sx={{display:"flex",margin: "2%",justifyContent:"center"}}>
                    Register
                    <div>
                        username: <input value={username} onChange={handleUsernameChange}/>
                    </div>
                    <div>
                        password: <input type="password" value={password} onChange={handlePasswordChange}/></div>
                    <div>
                        <Button type="submit" onClick={registerFunction} variant="contained" size="medium" sx={{margin: "2%"}}>Register</Button>
                    
                    </div>
                </form>
            </Grid> 
          </Box>
      </Container>
    </Paper>
  );
}



export default Register;