import axios from 'axios'
const baseUrl = '/api/tentit'
const registerURL = '/api/register'
const loginURL = '/api/login'

const register = (username, password) => {
  const request = axios.post(registerURL,{username:username, password:password})
  return request.then(response => {    
    return response  
  })
}

const login = (username, password) => {
  const request = axios.post(loginURL,{username:username, password:password})
  return request.then(response => {    
    return response  
  })
}

  const getAll = (token) => {
    const request = axios.get(baseUrl,{ headers: {Authorization : `Bearer ${token}`} })
    return request.then(response => {
      return response.data
    })
  }

const create = (token, newObject) => {
    const request = axios.post(baseUrl, newObject,{ headers: {Authorization : `Bearer ${token}`} })
    return request.then(response => {
      return response.data
    })
  }

const del = (token,id) => {
  let result = axios.delete(`${baseUrl}/${id}`,{},{ headers: {Authorization : `Bearer ${token}`} })
  .then((response => {
    return response.data}))
  .catch(error => console.log("err",error))
  return result.then(response => response)
}

const edit = (token,id,objekti) => {
  let result = axios.put(`${baseUrl}/${id}`,objekti,{ headers: {Authorization : `Bearer ${token}`} })
  .then((response => {
    return response.data}))
  .catch(error => console.log("err",error))
  return result.then(response => response)
}

let exportObject = { register, login, getAll, create, del, edit }

export default exportObject;